import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';
import { DtHeroDashboard } from '@src/controls/hero/DtHeroDashborad';

/**
 * Component: ViewHero
 */
@Component({
  components: {
    DtHeroDashboard,
  },
})
export default class ViewHero extends Vue {
  public get name(): string {
    return this.$route.params.name;
  }

  private render(h: CreateElement): VNode {
    return (
      <div staticClass="dt-view-hero">
        <dt-hero-dashboard hero={this.$db.heroMap[this.name]} />
      </div>
    );
  }
}
