import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';

/**
 * Component: HeroCard
 */
@Component
export class DtHeroCard extends Vue {
  @Prop({ type: String, required: true })
  public readonly name!: string;

  @Prop({ type: String, default: 'center' })
  public readonly position!: 'top' | 'center' | 'bottom';

  public isExpanding: boolean = false;
  public isCollapsing: boolean = false;

  public get classes(): ClassName {
    return [
      `dtp-position_${this.position}`,
      {
        'is-expanding': this.isExpanding,
        'is-collapsing': this.isCollapsing,
      },
    ];
  }

  private onMouseenter(event: MouseEvent): void {
    this.isExpanding = true;
    this.isCollapsing = false;
  }
  private onMouseleave(event: MouseEvent): void {
    this.isExpanding = false;
    this.isCollapsing = true;
  }

  private render(h: CreateElement): VNode {
    return (
      <router-link
        staticClass="dt-hero-card"
        class={this.classes}
        to={`/Hero/${this.name}`}
        nativeOnMouseenter={this.onMouseenter}
        nativeOnMouseleave={this.onMouseleave}
      >
        <div staticClass="dt-hero-card_container">
          <div staticClass="dt-hero-card_wrapper">
            <i staticClass="dt-hero-avatar-v dt-hero-card_avatar" class={this.name} />
            {this.isExpanding ? (
              <video staticClass="dt-hero-card_movie" muted autoplay loop>
                <source
                  src={this.$res(`/videos/heroes/${this.name}.webm`)}
                  type="video/webm"
                />
              </video>
            ) : (
              ''
            )}
          </div>
          <h3 staticClass="dt-hero-card_name">
            <span staticClass="dt-hero-card_name-content">
              {this.$loc.dict[this.name]}
            </span>
          </h3>
        </div>
      </router-link>
    );
  }
}
