import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';
import { Hero, Attribute, AttackCapability } from '@db/models/hero';
import { AbilityUnitDamageType } from '@db/models/ability';

/**
 * Component: HeroDashboard
 */
@Component
export class DtHeroDashboard extends Vue {
  @Prop({ type: Object, required: true })
  public readonly hero!: Hero;

  // tslint:disable:max-func-body-length
  private render(h: CreateElement): VNode {
    return (
      <div staticClass="dt-hero-dashboard">
        <dt-flexbox gutter direction="column" align="stretch">
          <dt-flexbox>
            <dt-card>
              <dt-flexbox gutter>
                <dt-flexbox flex="none">
                  <video staticClass="dt-hero-dashboard_movie" muted autoplay loop>
                    <source
                      src={this.$res(`/videos/heroes/${this.hero.name}.webm`)}
                      type="video/webm"
                    />
                  </video>
                </dt-flexbox>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox tag="h1">{this.$loc.dict[this.hero.name]}</dt-flexbox>
                  <dt-flexbox gutter>
                    <dt-flexbox>
                      <i
                        staticClass="dt-attribute-icon"
                        class={Attribute[this.hero.attributes.primary].toLowerCase()}
                      />
                    </dt-flexbox>
                    <dt-flexbox>复杂度：{this.hero.roles.complexity}</dt-flexbox>
                    <dt-flexbox>核心：{this.hero.roles.carry}</dt-flexbox>
                    <dt-flexbox>辅助：{this.hero.roles.support}</dt-flexbox>
                    <dt-flexbox>爆发：{this.hero.roles.nuker}</dt-flexbox>
                    <dt-flexbox>控制：{this.hero.roles.disabler}</dt-flexbox>
                    <dt-flexbox>打野：{this.hero.roles.jungler}</dt-flexbox>
                    <dt-flexbox>耐久：{this.hero.roles.durable}</dt-flexbox>
                    <dt-flexbox>逃生：{this.hero.roles.escape}</dt-flexbox>
                    <dt-flexbox>推进：{this.hero.roles.pusher}</dt-flexbox>
                    <dt-flexbox>先手：{this.hero.roles.initiator}</dt-flexbox>
                  </dt-flexbox>
                </dt-flexbox>
              </dt-flexbox>
            </dt-card>
          </dt-flexbox>
          <dt-flexbox>
            <dt-card>
              <dt-flexbox gutter>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox>属性</dt-flexbox>
                  <dt-flexbox>
                    <i staticClass="dt-attribute-icon strength" />
                    力量：
                    {this.hero.attributes.strength.base}
                    +
                    {this.hero.attributes.strength.gain}/级
                  </dt-flexbox>
                  <dt-flexbox>
                    <i staticClass="dt-attribute-icon agility" />
                    力量：
                    {this.hero.attributes.agility.base}
                    +
                    {this.hero.attributes.agility.gain}/级
                  </dt-flexbox>
                  <dt-flexbox>
                    <i staticClass="dt-attribute-icon intelligence" />
                    智力：
                    {this.hero.attributes.intelligence.base}
                    +
                    {this.hero.attributes.intelligence.gain}/级
                  </dt-flexbox>
                </dt-flexbox>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox>
                    攻击（
                    {this.hero.attack.capability ===
                    AttackCapability.DOTA_UNIT_CAP_MELEE_ATTACK
                      ? '近战'
                      : '远程'}
                    ）
                  </dt-flexbox>
                  <dt-flexbox>
                    攻击力： {this.hero.attack.damage.min} ~ {this.hero.attack.damage.max}
                  </dt-flexbox>
                  <dt-flexbox>攻击频率： {this.hero.attack.rate}</dt-flexbox>
                  <dt-flexbox>攻击间隔： {this.hero.attack.range}</dt-flexbox>
                  <dt-flexbox>攻击前摇： {this.hero.attack.animationPoint}</dt-flexbox>
                  {this.hero.attack.capability ===
                  AttackCapability.DOTA_UNIT_CAP_RANGED_ATTACK ? (
                    <dt-flexbox>弹道速度： {this.hero.attack.projectileSpeed}</dt-flexbox>
                  ) : (
                    ''
                  )}
                </dt-flexbox>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox>防御</dt-flexbox>
                  <dt-flexbox>防御：{this.hero.armor.armorPhysical}</dt-flexbox>
                  <dt-flexbox>魔法抗性：{this.hero.armor.magicalResistance}%</dt-flexbox>
                </dt-flexbox>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox>机动性</dt-flexbox>
                  <dt-flexbox>移动速度：{this.hero.movement.speed}</dt-flexbox>
                  <dt-flexbox>转身速度：{this.hero.movement.turnRate}%</dt-flexbox>
                  <dt-flexbox>
                    视野距离（日/夜）：{this.hero.vision.day}/{this.hero.vision.night}
                  </dt-flexbox>
                </dt-flexbox>
                <dt-flexbox gutter direction="column">
                  <dt-flexbox>生命与魔法</dt-flexbox>
                  <dt-flexbox>
                    生命：{this.hero.status.health} +{this.hero.status.healthRegen}
                  </dt-flexbox>
                  <dt-flexbox>
                    魔法：{this.hero.status.mana} +{this.hero.status.manaRegen}
                  </dt-flexbox>
                  <dt-flexbox>
                    视野距离（日/夜）：{this.hero.vision.day}/{this.hero.vision.night}
                  </dt-flexbox>
                </dt-flexbox>
              </dt-flexbox>
            </dt-card>
          </dt-flexbox>
          <dt-flexbox direction="column" gutter>
            {this.hero.abilities.map(ability => (
              <dt-flexbox>
                <i staticClass="dt-spell-icon" class={`${this.hero.name} ${ability}`} />
                <div>
                  {this.$db.abilityMap[ability] && [
                    <div>{this.$loc.dict[`DOTA_Tooltip_ability_${ability}`]}</div>,
                    <div>
                      {AbilityUnitDamageType[this.$db.abilityMap[ability].damageType]}
                    </div>,
                  ]}
                </div>
              </dt-flexbox>
            ))}
          </dt-flexbox>
        </dt-flexbox>
      </div>
    );
  }
}
