import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';
import { DtHeroCard } from '@src/controls/hero/DtHeroCard';

/**
 * Component: ViewHeroes
 */
@Component({
  components: {
    DtHeroCard,
  },
})
export default class ViewHeroes extends Vue {
  private render(h: CreateElement): VNode {
    return (
      <div staticClass="dt-view-heroes">
        <div staticClass="dt-view-heroes_wrapper">
          <dt-flexbox gutter>
            {this.$db.heroGroups.map(group => (
              <dt-flexbox flex="100" align="stretch">
                <dt-flexbox flex="none">
                  <div staticClass="dt-view-heroes_row-label">
                    <div staticClass="dt-view-heroes_row-label-container">
                      <div staticClass="dt-view-heroes_row-label-content">
                        {group.primary}
                      </div>
                    </div>
                  </div>
                </dt-flexbox>
                <dt-flexbox staticClass="dt-view-heroes_row">
                  {group.heroes.map(hero => (
                    <dt-flexbox staticClass="dt-view-heroes_cell" flex="none">
                      <dt-hero-card name={hero.name} />
                    </dt-flexbox>
                  ))}
                </dt-flexbox>
              </dt-flexbox>
            ))}
          </dt-flexbox>
        </div>
      </div>
    );
  }
}
